  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  padding-left: 45px;
  padding-right: 45px;
}
@media (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  padding: 10px;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  color: #011c40;
  text-align: center;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn:hover {
  opacity: 0.8;
}
.btn.focus-visible:focus {
  outline: 1px solid #011c40;
  outline-offset: -2px;
}
.btn:active {
  opacity: 0.5;
  transition: none;
}

.main-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;
}

.main-header__inner {
  display: flex;
  justify-content: space-between;
  padding-top: 34px;
  padding-bottom: 34px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .main-header__inner {
    padding-top: 21.5px;
    padding-bottom: 21.5px;
  }
}
@media (max-width: 767px) {
  .main-header__inner {
    position: relative;
    padding: 0;
    height: 62px;
  }
}

.main-header__mobile-wrapper {
  display: flex;
  align-items: flex-start;
}
@media (max-width: 1023px) {
  .main-header__mobile-wrapper {
    padding: 10px 0;
  }
}
@media (max-width: 767px) {
  .main-header__mobile-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    padding: 20px 15px;
    width: 100%;
    height: 100%;
    z-index: 3;
    justify-content: space-between;
    will-change: width;
    transition: width 0.5s ease-out, color 0.5s ease-out, background-color 0.5s ease-out;
  }
}

.main-header__mobile-wrapper.active {
  width: 285px;
  background-color: #f9fbfd;
  color: #011c40;
}

.main-header__logo-link {
  margin-right: 20px;
}
@media (max-width: 767px) {
  .main-header__logo-link[href]:focus {
    outline: 1px solid #011c40;
  }
}

.main-header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-header__toggle {
    display: block;
  }
}

.active .main-header__toggle {
  position: relative;
  left: 6px;
}

@media (max-width: 767px) {
  .main-header.no-js {
    position: static;
  }
}

@media (max-width: 767px) {
  .no-js .main-header__inner {
    height: auto;
    flex-direction: column;
  }
}

@media (max-width: 767px) {
  .no-js .main-header__mobile-wrapper {
    position: static;
    width: 100%;
    background-color: #011c40;
    color: #f9fbfd;
  }
}

.no-js .main-header__toggle {
  display: none;
}

.logo-link {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  color: inherit;
  line-height: 0;
}
.logo-link[href]:hover {
  opacity: 0.7;
}
.logo-link[href]:focus {
  outline: 1px solid #f9fbfd;
  outline-offset: 1px;
}
.logo-link[href]:active {
  transition: none;
  opacity: 0.4;
}

.logo-link svg {
  width: 70px;
  height: auto;
}
@media (max-width: 1023px) {
  .logo-link svg {
    width: 38px;
  }
}
@media (max-width: 767px) {
  .logo-link svg {
    width: 42px;
  }
}

.main-inner {
  min-height: 650px;
}

.main-intro {
  color: #f9fbfd;
  background-color: #003d78;
  background-image: url("../img/background/intro-bg-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
@media (max-width: 1023px) {
  .main-intro {
    filter: none;
    background-image: url("../img/background/intro-bg-tablet.jpg");
  }
}
@media (max-width: 767px) {
  .main-intro {
    background-size: 768px 638px;
    background-position: center top -40px;
  }
}
@media (min-resolution: 144dpi) and (max-width: 1023px), (min-resolution: 2dppx) and (max-width: 1023px) {
  .main-intro {
    background-image: url("../img/background/intro-bg-tablet@2x.jpg");
  }
}
@media (min-resolution: 144dpi) and (min-width: 1024px), (min-resolution: 2dppx) and (min-width: 1024px) {
  .main-intro {
    background-image: url("../img/background/intro-bg-desktop@2x.jpg");
  }
}

.main-intro__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 145px 95px 150px;
  min-height: 650px;
}
@media (max-width: 1023px) {
  .main-intro__inner {
    padding: 100px 0 107px;
    min-height: 638px;
  }
}
@media (max-width: 767px) {
  .main-intro__inner {
    padding: 65px 0 66px;
    min-height: 500px;
  }
}

.main-intro h1 {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main-intro h1 {
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .main-intro h1 {
    font-size: 36px;
    line-height: 45px;
  }
}

.main-intro p {
  margin: 0;
  font-size: 20px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main-intro p {
    font-size: 18px;
    line-height: 22px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .main-intro p {
    font-size: 16px;
    line-height: 1.4;
    margin-right: 60px;
  }
}

@media (max-width: 767px) {
  .main-nav {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    z-index: 2;
    overflow: hidden;
    visibility: hidden;
    pointer-events: none;
    transition: visibility 0.5s ease-out;
  }
}

.main-nav__overlay {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__overlay {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 43, 102, 0.5);
    opacity: 0;
    transition: opacity 0.3s ease;
  }
}

@media (max-width: 767px) {
  .main-nav__list-wrapper {
    position: absolute;
    top: 62px;
    right: 0;
    height: 100%;
    width: 285px;
    transform: translate3d(100%, 0, 0);
    transition: transform 0.5s ease-out;
  }
}

.main-nav__list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: nowrap;
    padding: 13px 17px 14px;
    height: calc(100% - 50px);
    overflow: auto;
    color: #011c40;
    background-color: #f9fbfd;
  }
}

.main-nav.active {
  pointer-events: all;
  visibility: visible;
}

.active .main-nav__list-wrapper {
  transform: translate3d(0, 0, 0);
}

.active .main-nav__overlay {
  opacity: 1;
}

.main-nav__item {
  margin-left: 36px;
}
@media (max-width: 1023px) {
  .main-nav__item {
    margin-left: 26px;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    margin-bottom: 8px;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .main-nav__item:last-child {
    margin-bottom: 0;
  }
}

.main-nav__link {
  display: inline-block;
  padding: 10px 0;
  font-size: 16px;
  line-height: 20px;
  color: inherit;
  vertical-align: middle;
  transition: opacity 0.3s ease;
}
@media (max-width: 1023px) {
  .main-nav__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .main-nav__link {
    font-size: 18px;
    line-height: 22px;
    padding: 10px 8px;
  }
}
.main-nav__link:hover {
  opacity: 0.7;
}
.main-nav__link.focus-visible:focus {
  outline: 1px solid #f9fbfd;
  outline-offset: 1px;
}
@media (max-width: 767px) {
  .main-nav__link.focus-visible:focus {
    outline: 1px solid #011c40;
  }
}
.main-nav__link:active {
  transition: none;
  opacity: 0.4;
}

@media (max-width: 767px) {
  .no-js .main-nav {
    position: static;
    pointer-events: all;
    overflow: visible;
    visibility: visible;
  }
}

.no-js .main-nav__overlay {
  display: none;
}

@media (max-width: 767px) {
  .no-js .main-nav__list-wrapper {
    position: static;
    width: 100%;
    transform: none;
  }
}

@media (max-width: 767px) {
  .no-js .main-nav__list {
    padding: 10px;
    transform: none;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    color: #f9fbfd;
    background-color: #011c40;
    overflow: visible;
  }
}

.no-js .main-nav__item {
  margin-bottom: 0;
  margin-right: 10px;
}

.nav-toggle {
  position: relative;
  width: 24px;
  height: 24px;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
.nav-toggle::after, .nav-toggle::before {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #f9fbfd;
  content: "";
  transition: background-color 0.5s ease-out, transform 0.5s ease-out;
}
.nav-toggle::after {
  top: 17px;
}
.nav-toggle::before {
  top: 5px;
}
.nav-toggle > span {
  position: absolute;
  top: 11px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #f9fbfd;
  transition: background-color 0.5s ease-out, transform 0.5s ease-out;
}
.nav-toggle:hover {
  opacity: 0.7;
}
.nav-toggle.focus-visible:focus {
  outline: 1px solid #f9fbfd;
  outline-offset: 1px;
}
.nav-toggle:active {
  transition: none;
  opacity: 0.4;
}

.nav-toggle.active::after, .nav-toggle.active::before, .nav-toggle.active > span {
  background-color: #011c40;
}
.nav-toggle.active::after {
  transform: translate3d(3px, -6px, 0) rotate(45deg);
  width: 18px;
}
.nav-toggle.active::before {
  transform: translate3d(3px, 6px, 0) rotate(-45deg);
  width: 18px;
}
.nav-toggle.active > span {
  transform: scaleX(0);
}
.nav-toggle.active.focus-visible:focus {
  outline: 1px solid #011c40;
}

.main-footer {
  background-color: #011c40;
